* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	line-height: 1;
	font-family: Arial, sans-serif;
	background: #222222;
}

a {
	text-decoration: none;
	color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
	font-weight: normal;
}

.splash-container {
	max-width: 100%;
	text-align: center;
}

.splash-container img {
	max-width: 80%;
}